import MiniCore from "easemob-websdk/src/miniCore";
import * as contactPlugin from "easemob-websdk/src/apis/index";
import * as chatroomPlugin from "easemob-websdk/src/apis/chatRoomApi";
import * as groupPlugin from "easemob-websdk/src/apis/groupApi";
import * as presencePlugin from "easemob-websdk/src/apis/presenceApi";
import * as LocalCacheAPI from "easemob-websdk/src/localCache/index";
import {
  appKey,
  url,
  restUrl,
  isHttpDNS,
  useCustomConfig,
  useReplacedMessageContents,
  DEFAULT_APPKEY
} from "@/const/index";

const customOption = {
  appKey,
  // delivery: true,
  url: url || `wss://im-api-new-hsb.easemob.com/websocket`,
  apiUrl: restUrl || `${window.location.protocol}//a1-hsb.easemob.com`,
  useReplacedMessageContents,
  isHttpDNS
};

const option = {
  appKey: DEFAULT_APPKEY
};

const conn = new MiniCore(useCustomConfig ? customOption : option);
conn.usePlugin(contactPlugin, "contact");
conn.usePlugin(chatroomPlugin, "chatroom");
conn.usePlugin(groupPlugin, "group");
conn.usePlugin(presencePlugin, "presence");
conn.usePlugin(LocalCacheAPI, "localCache");
//@ts-ignore
window.conn = conn;

export default conn;
